body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: 100vh;
}

#mainContent {
  -webkit-flex: 1 1;
          flex: 1 1;
}

#footerContent {
  display: block;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 10vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: calc(10vh + 2vw);
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(1vh + 2vw);
  color: white;
}

.App-header-div {
  font: inherit;
}

.App-header-div-logo {
  float: left;
  margin: calc(1vh + 0.2vw) 1rem 0px 0rem;
  vertical-align: middle;
}

.App-header-div-title {
  float: left;
}

.App-header-div-title h1 {
  margin: calc(1.6vh + 0.2vw) 0px 0px 0rem;
  vertical-align: middle;
}

.App-link {
  color: #61dafb;
}

section.AppSection {
  margin: 0px 20px 20px 0px;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

div.theHelp {
  float: left;
}

div.theAbout {
  float: right;
}

div.pharma-person {
  text-align: justify;
  padding-left: 1em;
}

.pharma-control-title{
  font-weight: bold;
}

.pharma-btn {
  display: inline-block;
  border: none;
  border-radius: 10px;
  padding: 0.2em 0.6em;
  margin: 0;
  text-decoration: none;
  background: black;
  color: #ffffff;
  border: 1px solid black;
  font-family: sans-serif;
  font-size: 1rem;
  cursor: pointer;
  text-align: center;
  transition: background 250ms ease-in-out, 
              -webkit-transform 150ms ease;
  transition: background 250ms ease-in-out, 
              transform 150ms ease;
  transition: background 250ms ease-in-out, 
              transform 150ms ease, 
              -webkit-transform 150ms ease;
  -webkit-appearance: none;
  -moz-appearance: none;
  -webkit-filter: saturate(0.50);
          filter: saturate(0.50);
  margin-left: 1rem;
  margin-bottom: 3px;
}

.pharma-btn:hover,
.pharma-btn:focus {
    -webkit-filter: saturate(0.90);
            filter: saturate(0.90);
    background: white;
    color: black;
    border: 1px solid black;
}

.pharma-btn:focus {
  outline: 2px solid green;
  outline-offset: -4px;
}

.pharma-btn:active {
  -webkit-transform: scale(0.99);
          transform: scale(0.99);
  -webkit-filter: saturate(0.99);
          filter: saturate(0.99);
}

.pharma-btn-prn {
  text-decoration: none;
}

.pharma-btn-add {
  text-decoration: none;
}

.plain-list {
  list-style: none;
}

.pharma-btn-edit {
  text-decoration: none;
}

.pharma-btn-help {
  text-decoration: none;
}

.pharma-btn-about {
  text-decoration: none;
}

.pharma-btn-update {
  text-decoration: none;
}

.pharma-btn-cancel {
  text-decoration: none;
}

.pharma-btn-delete {
  text-decoration: none;
}

.pharma-btn-restock {
  text-decoration: none;
}

.pharma-person {
  padding-bottom: 20px;
  padding-top: 5px;
  margin-bottom: 10px;
  border-radius: 0px 20px 20px 0px;
}

.pharma-person-medicine {
  border: solid 2px white;
  background-color: #ffffff88;
  border-radius: 15px 0px 0px 15px;
  margin-left: 20px;
  margin-bottom: 3px;
  padding-left: 10px;
}

.theHelpDiv {
  margin: 20px;
  border-radius: 20px;
  padding: 10px;
  background-color: white;
  border: 3px solid magenta;
  text-align: left;
}

.theAboutDiv {
  margin: 20px;
  border-radius: 20px;
  padding: 10px;
  background-color: white;
  border: 3px solid cyan;
  text-align: left;
}

.pharma-person-medicine-edit {
  float: right;
  margin-top: 10px;
  text-align: right;
  margin-right: 10px;
}

.pharma-person-control {
  margin: 20px;
  border-radius: 20px;
  padding: 10px;
}

.pharma-medicine-control {
  margin: 20px;
  border-radius: 20px;
  padding: 10px;
}

.pharma-person-add {
  background-color: white;
  border: 3px solid green;
}

.pharma-person-edit {
  background-color: white;
  border: 3px solid blue;
}

.pharma-person-delete {
  background-color: white;
  border: 3px solid red;
}

.pharma-medicine-add {
  background-color: white;
  border: 3px solid green;
}

.pharma-medicine-prn {
  background-color: white;
  border: 3px solid seagreen;
}

.pharma-medicine-edit {
  background-color: white;
  border: 3px solid blue;
}

.pharma-medicine-restock {
  background-color: white;
  border: 3px solid yellow;
}

.pharma-medicine-delete {
  background-color: white;
  border: 3px solid red;
}

p.pharma-delete-person-para {
  float: right;
  margin-right: 20px;
}

table.pharma-edit-layout {
  text-align: center;
  margin-left: auto;
  margin-right: auto;
}

td.pharma-edit-layout-label {
  text-align: right;
  font-weight: bold;
}

td.pharma-edit-layout-control {
  text-align: left;
}

td.pharma-edit-layout-error {
  text-align: left;
  font-weight: bold;
  color: red;
}

.App-footer {
  background-color: black;
  min-height: 3vh;
  max-height: 5vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  /*font-size: calc(4px + 2vmin);*/
  font-size: calc(0.75rem);
  color: white;
  font-style: italic;
}

.App-footer-version{
  display:inline;
  margin: 0px 5px 0px 5px;
}

.App-footer-copyright{
  display:inline;
  margin: 0px 5px 0px 5px;
}

span.emoji-prefix {
  margin-right: 0.5rem;
}
